// src/renderer/components/LogContent.tsx
import React, { useEffect, useRef } from 'react';

interface LogMessage {
  id: string; //  timestamp,   
  text: string;
  time: string;
}

interface LogContentProps {
  messages: LogMessage[];
}

function LogContent({ messages }: LogContentProps) {
  const endOfMessagesRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    //     
    endOfMessagesRef.current?.scrollIntoView({ behavior: "smooth" });
  }, [messages]); //    

  if (!messages || messages.length === 0) {
    return <div className="p-4 text-gray-500"> .</div>;
  }

  return (
    <div className="p-4 overflow-y-auto h-full flex flex-col"> {/* flex-col    */}
      {messages.map((msg) => (
        <div key={msg.id} className="mb-2">
          <span className="text-xs text-gray-500 mr-2">{msg.time}</span>
          <span className="whitespace-pre-wrap">{msg.text}</span>
        </div>
      ))}
      <div ref={endOfMessagesRef} /> {/*    */}
    </div>
  );
}

export default LogContent;
export type { LogMessage }; //   