// src/renderer/utils/textProcessing.ts
//   formatGoalsForExport, parseImportedText ...

export interface ParsedField {
  name: string;
  value: string;
}

export interface GoalParsedData {
  displayableFields: ParsedField[];
  rating?: number;
  ratingLabel?: string;
}

export function parseGoalData(text: string): GoalParsedData {
  const fieldRegex = /\[([\w_]+?)::([^\]]+?)\]/g;
  const allParsedFields: ParsedField[] = [];
  //     ,       
  const extractedValues: {
    [key: string]: { value: number; originalValue: string; fieldName: string };
  } = {};

  let match;
  while ((match = fieldRegex.exec(text)) !== null) {
    const fieldName = match[1];
    const fieldValue = match[2];
    allParsedFields.push({ name: fieldName, value: fieldValue });

    const numValue = parseFloat(fieldValue);
    if (!isNaN(numValue)) {
      extractedValues[fieldName.toLowerCase()] = {
        value: numValue,
        originalValue: fieldValue,
        fieldName: fieldName,
      };
    }
  }

  let rating: number | undefined;
  let ratingLabel: string | undefined;

  //  : parent_value * impact / costs
  const pValData = extractedValues["parent_value"];
  const impactData = extractedValues["impact"];
  const costsData = extractedValues["costs"];

  if (pValData && impactData && costsData) {
    if (costsData.value !== 0) {
      rating = (pValData.value * impactData.value) / costsData.value;
      ratingLabel = " (P*I/C)";
    } else {
      //    
      const numerator = pValData.value * impactData.value;
      rating = numerator > 0 ? Infinity : numerator < 0 ? -Infinity : 0;
      ratingLabel = "P*I/C (=0)";
    }
  } else {
    //  : [rating::value], [value::value], [priority::value]  [p::value]
    const directRatingData =
      extractedValues["rating"] ??
      extractedValues["value"] ??
      extractedValues["priority"] ??
      extractedValues["p"];
    if (directRatingData) {
      rating = directRatingData.value;
      //      ,        
      const labelKey = Object.keys(extractedValues).find(
        (k) => extractedValues[k] === directRatingData,
      );
      ratingLabel = labelKey ? extractedValues[labelKey].fieldName : "";
    } else {
      //  : impact / costs (  impact)
      //  impactData/costsData,      P*I/C
      if (impactData) {
        if (costsData) {
          //  impact  costs,   parent_value
          if (costsData.value !== 0) {
            rating = impactData.value / costsData.value;
            ratingLabel = "Impact/Costs";
          } else {
            rating =
              impactData.value > 0
                ? Infinity
                : impactData.value < 0
                  ? -Infinity
                  : 0;
            ratingLabel = "Impact/Costs (=0)";
          }
        } else {
          //   impact
          rating = impactData.value;
          ratingLabel = "Impact";
        }
      } else if (costsData) {
        //   costs ( impact, parent_value)
        //    ,      
        rating = -costsData.value;
        ratingLabel = ` (.)`;
      }
    }
  }

  // displayableFields -    [name::value],     .
  //    " "     .
  const displayableFields = allParsedFields;

  return {
    displayableFields,
    rating,
    ratingLabel,
  };
}

